.frequently-asked-questions {
  padding-bottom: 80px;
  h2 {
    padding-bottom: 32px;
  }

  .owl-carousel {
    .owl-stage-outer {
      overflow: visible;
    }

    .owl-item {
      opacity: 0;
      transition: opacity 0.3s ease;

      &.active {
        opacity: 1;
      }
    }
  }

  .owl-theme .owl-nav {
    z-index: 999;
    position: absolute;
    width: 100%;
    height: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;

    [class*=owl-] {
      border-radius: 43px;

      &:hover {
        background: $white-default;
      }
    }

    button {
      position: absolute;
      display: inline-block;
      top: 0;
      bottom: 0;
      width: 44px;
      height: 44px;
      background: $white-default;
      box-shadow: 0 1px 7px fade-out($blackOne, 0.91);
      cursor: pointer;
      margin-top: auto;
      margin-bottom: auto;
      transition: opacity 0.3s ease,
      box-shadow 0.2s ease-in-out;
      @media screen and (min-width: 428px) {
        width: 44px;
        height: 44px;
      }

      &:hover {
        box-shadow: 0 1px 7px fade-out($blackOne, 0.95);
      }

      span {
        position: relative;
        display: block;
        height: 100%;
        vertical-align: middle;
      }

      svg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 10px;
        height: 6.17px;
        vertical-align: middle;
        @media screen and (min-width: 428px) {
          width: 6.59px;
          height: 11.17px;
        }
      }
    }

    button.owl-prev,
    button.owl-next {
      background: $white-default;
    }

    button.owl-prev {
      left: 0;
      margin-left: -1px;

      svg {
        transform: rotate(-180deg);
      }
    }

    button.owl-next {
      right: 0;
      margin-right: -1px;
    }

    .disabled {
      opacity: 0;
      transform: translateX(100%);
    }
  }

  .item {
    display: block;
    width: 100%;
    max-width: 282px;
    height: 160px;
    box-shadow: 0 20px 60px 0 fade-out($black-default, 0.95);
    border-radius: 16px;
    background-color: $white-default;

    &__wrap {
      overflow: hidden;
      height: 100%;
      padding-right: 16px;
      padding-left: 16px;
    }

    &__title {
      float: left;
      padding-top: 16px;
      padding-bottom: 8px;
      font-size: 20px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-weight: 600;
      line-height: 24px;
      vertical-align: top;
    }

    &__subtitle {
      width: 100%;
      vertical-align: top;
      box-sizing: border-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 15px;
      font-family: "MuseoSansCyrl-500", sans-serif;
      font-weight: 400;
      line-height: 24px;
      color: #949494;
      max-height: 20px;
      word-break: break-word;
    }

    &__text-group {
      float: left;
      width: calc(100% - 109px);
      padding-bottom: 19px;
    }

    &__img {
      float: right;
      height: 100%;
      vertical-align: top;
    }

  }
}
