.support-is-here {
  margin-bottom: 82px;

  .container-fluid {
    position: relative;
    border-radius: 40px;
    background-color: #93BBEA;
  }

  &__wrap {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    padding: 40px 15px 54px;
  }

  h1, h4 {
    text-align: center;
    color: $white-default;
  }

  h1 {
    padding-bottom: 4px;
  }

  h4 {
    padding-bottom: 40px;
  }

  &__btn-group {
    text-align: center;

    button, a {
      display: block;
      width: 100%;
      height: 48px;
      padding: 15px 5px;
      border-radius: 12px;
      background-color: $white-default;
      font-size: 15px;
      font-weight: 600;
      font-family: "MuseoSansCyrl-700", sans-serif;
      line-height: 18px;
      text-align: center;
      vertical-align: middle;

      svg {
        margin-right: 8px;
        vertical-align: middle;
      }
    }
  }

  &__btn {
    display: inline-block;
    width: 100%;
    max-width: 160px;
    padding-right: 4px;
    padding-left: 4px;
  }

  &__bg {
    width: 100%;
    height: 100%;
  }
}
