.container-fluid {
  position: relative;
  display: block;
  max-width: 1200px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.container {
  position: relative;
  display: block;
  max-width: 996px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  @media screen and (max-width: 1140px) {
    padding-right: 15px;
    padding-left: 15px;
  }
  @media screen and (max-width: $little_smart) {
    padding-right: 9px;
    padding-left: 9px;
  }

  @media screen and (min-width: 0px) {
    max-width: 100%;
  }

  @media screen and (min-width: 576.98px) {
    max-width: 576px;
  }

  @media screen and (min-width: 768.98px) {
    max-width: 768px;
  }

  @media screen and (min-width: 992.98px) {
    max-width: 908px;
  }

  @media screen and (min-width: 1045px) {
    max-width: 996px;
  }

  @media screen and (min-width: 1280.98px) {
    max-width: 996px;
  }

  &_pt-110 {
    @media screen and (min-width: 1280px) {
      padding-top: 110px;
    }
  }
}
