.reference {
  margin-bottom: 80px;

  &__btn {
    overflow: hidden;
    position: relative;
    height: 64px;
    padding: 20px 50px 20px 24px;
    box-shadow: 0 0 30px fade-out($black-default, 0.95);
    border-radius: 16px;
    background-color: $white-default;
    font-size: 20px;
    font-weight: 600;
    font-family: "MuseoSansCyrl-700", sans-serif;
    line-height: 24px;

    &:not(:first-child) {
      margin-top: 12px;
    }

    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: 24.42px;
      bottom: 0;
      left: auto;
      width: 6.59px;
      height: 11.17px;
      margin-top: auto;
      margin-bottom: auto;
      background: url("#{$way-img}sprites/svg/arrow-gray.svg") no-repeat center / 100%;
    }

    &.selected {
      height: 100%;

      .reference__btn__content {
        display: block;
      }
    }

    &__title {
      padding-bottom: 4px;
      font-size: 20px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-weight: 600;
      line-height: 24px;
    }

    &__content {
      position: relative;
      display: none;
      height: 38px;
      width: 100%;
      margin: 0 auto;
      font-size: 13px;
      font-family: "MuseoSansCyrl-500", sans-serif;
      font-weight: 400;
      line-height: 18.2px;
      color: #949494;

      &.open {
        height: auto;
        background-color: $white-default;
      }

      .reference__inner {
        padding: 5px 0 38px 40px;

        p {
          opacity: 0;
          margin: 0;
          padding-top: 10px;
          font-size: 12px;
          font-weight: 400;
          line-height: $line_height150;
          color: $black-default;
          transition: opacity 0.2s ease;
          @media screen and (min-width: 1440.98px) {
            font-size: 14px;
          }
        }
      }
    }
  }


}
