.social-media-and-community {
  margin-bottom: 80px;

  .container {
    width: 100%;
    padding: 42px 40px;
    border-radius: 40px;
    box-shadow: 0 20px 60px 0 fade-out($black-default, 0.95);
    background-color: $white-default;
  }

  .title {
    padding-bottom: 0;
    text-align: left;
  }

  .subtitle {
    padding-bottom: 16px;
  }

  &__block-text, &__social-networks {
    display: inline-block;
    width: 50%;
    vertical-align: middle;
  }

  &__social-networks {
    margin-right: -5px;
    text-align: right;

    a {
      position: relative;
      display: inline-block;
      width: 56px;
      height: 56px;
      margin-right: 7px;
      margin-left: 7px;
      border-radius: 13.33px;
      text-align: center;
      vertical-align: middle;
      background-color: #F9F9F9;

      svg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
      }
    }
  }
}
